*,
::before,
::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  background: #090C21 url("../media/bg-pattern.png");
  color: white;
}

@media (max-width: 767px) {
  body {
    background: #07091D;
  }
}

.parallax {
  scroll-behavior: smooth;
  perspective: 1px;
  height: 100vh;
  overflow-x: hidden;
  overflow-y: auto;
}

.parallax__group {
  scroll-behavior: smooth;
  position: relative;
  transform-style: preserve-3d;
}

.parallax__layer {
  /*position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;*/
}

.parallax__layer--base {
  transform: translateZ(0);
}

.parallax__layer-back {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: translateZ(0.35px) scale(0.7);
}

.parallax__layer-back--1 {
  left: calc((100vw - 1400px) / 2 + 50px);
  bottom: 500px;
  width: 125px;
  height: 125px;
}

.parallax__layer-back--2 {
  right: calc((100vw - 1400px) / 2 + 65px);
  top: 0;
  width: 90px;
  height: 90px;
  transform: translateZ(0.19px) scale(0.75);
}

.parallax__layer-back--3 {
  right: calc((100vw - 1400px) / 2 + 50px);
  top: 0;
  width: 90px;
  height: 90px;
  transform: translateZ(0.22px) scale(0.75);
}

.parallax__layer-back--4 {
  right: calc((100vw - 1400px) / 2 + 150px);
  bottom: 0;
  width: 40px;
  height: 40px;
  transform: translateZ(0.15px) scale(0.75);
}

.parallax__layer-back--5 {
  left: calc((100vw - 1400px) / 2 - 35px);
  bottom: -110px;
  width: 130px;
  height: 130px;
  transform: translateZ(0.18px) scale(0.85);
}

.parallax__layer-back--6 {
  left: calc((100vw - 1400px) / 2 + 50px);
  bottom: -100px;
  width: 90px;
  height: 90px;
  transform: translateZ(0.22px) scale(0.75);
}

.parallax__layer-back--7 {
  left: calc((100vw - 1400px) / 2 - 90px);
  top: 50%;
  width: 70px;
  height: 70px;
  transform: translateZ(0.16px) scale(0.75);
}

.parallax__layer-back--8 {
  right: calc((100vw - 1400px) / 2 - 30px);
  bottom: 200px;
  width: 85px;
  height: 85px;
  transform: translateZ(0.25px) scale(1);
}

.parallax__layer-back--9 {
  right: calc((100vw - 1400px) / 2 + 50px);
  bottom: -400px;
  width: 90px;
  height: 90px;
  transform: translateZ(0.22px) scale(0.75);
}

.parallax__layer-back--10 {
  left: calc((100vw - 1400px) / 2 + 350px);
  top: 0;
  width: 40px;
  height: 40px;
  transform: translateZ(0.15px) scale(0.85);
}

.parallax__layer-back--11 {
  right: calc((100vw - 1400px) / 2 + 150px);
  bottom: 200px;
  width: 40px;
  height: 40px;
  transform: translateZ(0.18px) scale(0.95);
}

.parallax__layer-back--12 {
  left: calc((100vw - 1400px) / 2 + 200px);
  bottom: -100px;
  width: 90px;
  height: 90px;
  transform: translateZ(0.22px) scale(0.85);
}

.parallax__layer-back--13 {
  right: calc((100vw - 1400px) / 2 + 250px);
  top: -150px;
  width: 90px;
  height: 90px;
  transform: translateZ(0.25px) scale(0.75);
}

.parallax__layer-back--14 {
  left: calc((100vw - 1400px) / 2 + 200px);
  top: 150px;
  width: 50px;
  height: 50px;
  transform: translateZ(0.18px) scale(0.85);
}

.parallax__layer-back--15 {
  right: calc((100vw - 1400px) / 2 + 270px);
  bottom: -50px;
  width: 40px;
  height: 40px;
  transform: translateZ(0.15px) scale(0.75);
}

.parallax__layer-ball {
  width: 100%;
  height: 100%;
  background-image: url("../media/ball.png");
  background-repeat: no-repeat;
  background-size: contain;
}

.parallax__layer-small-ball {
  width: 100%;
  height: 100%;
  background-image: url("../media/small-ball.png");
  background-repeat: no-repeat;
  background-size: contain;
}

.page {
  display: block;
  overflow: hidden;
}

.page__container {
  width: 100%;
  max-width: 1400px;
  padding: 0 20px;
  margin: 0 auto;
  box-sizing: border-box;
  position: relative;
  z-index: 1;
}

.page__full-screen {
  min-height: 800px;
  height: 100vh;
  display: flex;
  flex-direction: column;
  padding-top: 69px;
}

@media (max-width: 1280px) {
  .page__full-screen {
    padding-top: 33px;
  }
}

[class^="page__bg-"] {
  pointer-events: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  user-select: none;
}

@media (max-width: 767px) {
  .page__bg-mob-none {
    display: none;
  }
}

.page__bg-1 {
  position: absolute;
  left: 0;
  top: 0;
}

@media (max-width: 767px) {
  .page__bg-1 {
    width: 215vw;
  }
}

.page__bg-2 {
  position: absolute;
  right: 0;
  top: 0;
}

.page__bg-3 {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

@media (max-width: 767px) {
  .page__bg-3 {
    width: 135vw;
    top: 32%;
  }
}

.page__bg-4 {
  position: absolute;
  left: 0;
  top: 410px;
}

.page__bg-5 {
  position: absolute;
  right: 0;
  top: 235px;
}

.page__bg-6 {
  position: absolute;
  left: 50%;
  bottom: -555px;
  transform: translateX(-50%);
}

@media (max-width: 600px) {
  .page__bg-6 {
    width: 300vw;
    bottom: -90vw;
  }
}

.page__bg-7 {
  position: absolute;
  left: 0;
  top: 150px;
}

.page__bg-8 {
  position: absolute;
  top: 350px;
  right: -140px;
  transform: rotate(180deg);
}

.page__bg-9 {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.page__bg-10 {
  position: absolute;
  top: calc(50% - 400px);
  left: calc(50% - 950px);
  transform: translate(-50%, -50%);
}

.page__bg-11 {
  position: absolute;
  top: -400px;
  left: 0;
}

.page__bg-12 {
  position: absolute;
  top: 350px;
  right: 0;
  transform: rotate(180deg);
}

.page__bg-13 {
  position: absolute;
  top: 80%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.page__bg-14 {
  position: absolute;
  top: 60%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.page__bg-15 {
  position: absolute;
  top: -20px;
  right: 0;
}

.page__bg-16 {
  position: absolute;
  top: 50%;
  left: calc(50% + 800px);
  transform: translate(-50%, -50%);
}

.page__bg-17 {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

@media (max-width: 767px) {
  .page__bg-17 {
    width: 1800px;
    height: auto;
  }
}

.header {
  z-index: 1;
}

.header__flex {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

@media (max-width: 1280px) {
  .header__logo {
    width: 84px;
  }
}

.header__btn {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 80px;
  text-decoration: unset;
  color: white;
  width: 170px;
  height: 48px;
}

@media (max-width: 1280px) {
  .header__btn {
    display: none;
  }
}

.header__btn:before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url("../media/header-btn-border.svg");
  background-repeat: no-repeat;
  background-size: 100% 100%;
}

.header__hamburger-wrap {
  position: relative;
  width: 38px;
  height: 38px;
  cursor: pointer;
  display: none;
}

@media (max-width: 1280px) {
  .header__hamburger-wrap {
    display: block;
  }
}

.b-a, .header__hamburger, .header__hamburger:before, .header__hamburger:after {
  transition: all .15s ease-in;
  content: "";
  position: absolute;
  left: 0;
  height: 2px;
  width: 21px;
  border-radius: 15px;
  background-color: rgba(255, 255, 255, 0.95);
}

.header__hamburger {
  left: 14px;
  top: 17px;
  cursor: pointer;
}

.header__hamburger:before {
  top: -5px;
}

.header__hamburger:after {
  top: 5px;
  width: 14px;
  left: 7px;
}

.b-a-expanded, .header__hamburger-wrap.active .header__hamburger:before, .header__hamburger-wrap.active .header__hamburger:after {
  transition: all .25s;
  top: -0px;
}

.header__hamburger-wrap.active .header__hamburger {
  background-color: transparent;
  transition: all .35s;
}

.header__hamburger-wrap.active .header__hamburger:before {
  transform: rotate(45deg);
}

.header__hamburger-wrap.active .header__hamburger:after {
  transform: rotate(-45deg);
  left: 0;
  width: 21px;
}

