.mob-menu {
  flex-grow: 1;
  display: none;
}

@media (max-width: 1280px) {
  .mob-menu {
    position: fixed;
    z-index: 11;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.8);
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(40px);
    transform: translateX(100%);
    opacity: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: 0.3s ease-out;
    padding-top: 200px;
    padding-bottom: 100px;
    overflow-y: auto;
  }
  .mob-menu.active {
    animation: showMenu 0.6s;
    animation-fill-mode: forwards;
    opacity: 1;
  }
  @keyframes showMenu {
    0% {
      transform: translateX(0);
      opacity: 0;
    }
    50% {
      transform: translateX(0);
      opacity: 0;
    }
    100% {
      transform: translateX(0);
      opacity: 1;
    }
  }
  .mob-menu .nav {
    display: flex;
    flex-direction: column;
    margin-left: unset;
    max-width: unset;
    width: auto;
  }
  .mob-menu .nav__item {
    color: rgba(255, 255, 255, 0.5);
    font-size: 21px;
    margin-bottom: 62px;
    text-align: center;
  }
  .mob-menu .nav__item:not(:last-of-type)::before {
    content: "";
    position: absolute;
    top: calc(100% + 33px);
    left: 50%;
    transform: translateX(-50%);
    width: 5px;
    height: 5px;
    border-radius: 50%;
    background-color: rgba(255, 255, 255, 0.5);
  }
  .mob-menu .nav__item:after {
    content: "";
    position: absolute;
    top: calc(100% + 10px);
    left: 50%;
    transform: translateX(-50%);
    width: 34px;
    height: 43px;
    background-image: url("../media/mob-menu-icon.svg");
    background-repeat: no-repeat;
    background-position: center;
    opacity: 0;
    transition: 0.3s;
  }
  .mob-menu .nav__item:focus, .mob-menu .nav__item:active {
    color: white;
  }
  .mob-menu .nav__item:focus:not(:last-of-type)::after, .mob-menu .nav__item:active:not(:last-of-type)::after {
    opacity: 1;
  }
}

.mob-menu__close-btn-wrap {
  position: fixed;
  top: 33px;
  right: 20px;
  width: 38px;
  height: 38px;
  opacity: 0;
}

.mob-menu.active .mob-menu__close-btn-wrap {
  opacity: 1;
  animation: btn-show 0.55s;
  animation-fill-mode: forwards;
}

@keyframes btn-show {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.mob-menu__close-btn {
  transition: all .15s ease-in;
  position: absolute;
  top: 17px;
  left: 14px;
  height: 2px;
  width: 21px;
  border-radius: 15px;
  background-color: transparent;
}

.mob-menu__close-btn::before {
  content: "";
  position: absolute;
  left: 0;
  height: 2px;
  width: 21px;
  border-radius: 15px;
  background-color: rgba(255, 255, 255, 0.95);
  transition: all .25s;
  top: 0;
  transform: rotate(45deg);
}

.mob-menu__close-btn::after {
  content: "";
  position: absolute;
  height: 2px;
  border-radius: 15px;
  background-color: rgba(255, 255, 255, 0.95);
  transform: rotate(-45deg);
  left: 0;
  width: 21px;
  transition: all .25s;
  top: 0;
}

.mob-menu__logo {
  position: absolute;
  top: 36px;
  left: 20px;
}
